export default{
	data(){
		return{
			 providerList: []
		}
	},
	onLoad() {
		// 获取服务供应商。
	    uni.getProvider({
	        service: 'oauth',
	        success: (result) => {
				console.log('5555')//为啥进入不了？不执行这个成功函数
	            this.providerList = result.provider.map((value) => {
	                let providerName = '';
					let icon = ''
	                switch (value) {
	                    case 'weixin':
	                        providerName = '微信登录'
							icon='weixin'
	                        break;
	                    case 'qq':
	                        providerName = 'QQ登录'
							icon='QQ'
	                        break;
	                    case 'sinaweibo':
	                        providerName = '新浪微博登录'
							icon='xinlangweibo'
	                        break;

	                    case 'alipay':
	                        providerName = '支付宝登录'
							icon=''
	                        break;
	                    case 'apple':
	                        providerName = '苹果登录'
							icon=''
	                        break;
	                }
					console.log(icon)
	                return {
	                    name: providerName,
						icon:icon,
	                    id: value
	                }
	            });
	
	        },
	        fail: (error) => {
	            console.log('获取登录通道失败', error);
	        }
	    });
	},
	methods:{
		tryevent(){
			console.log(this.providerList)
		}
	}
}
